@import './colors.css';

* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background-color: var(--backgroundDark);
}

/*Navigation*/
.nav-style {
  padding: 1rem 1rem 0 1rem;
}

.searchbar-style {
  margin-right: 1rem;
}

/* Base Theme AKA Dark Mode*/

/*Main*/
h1 {
  font-family: 'Press Start 2P', cursive;
  color: var(--offWhite);
}

.app-description {
  text-align: left;
}

h2 {
  font-family: 'Share Tech Mono', monospace;
  color: var(--offWhite);
}

h4 {
  font-family: 'Press Start 2P', cursive;
  vertical-align: top;
}

#console-wars {
  font-family: 'VT323', monospace;
  font-weight: bolder;
  font-size: 3rem;
  color: var(--offWhite);
}

.project-title {
  padding: 3rem;
}

#anime-results {
  font-size: 3rem;
  font-family: 'Share Tech Mono', monospace;
  color: var(--offWhite);
  text-align: center;
  padding-bottom: 5px;
}

/* Hide  */

/*Search Results Game*/
.image {
  height: 15rem;
  width: 15rem;
}

.anime-results-link-div {
  font-size: 1.25rem;
}

#anime-results {
  margin-top: 10vh;
}

.search-results h3 {
  font-family: 'VT323', monospace;
  color: var(--playstationBlue);
  text-decoration: underline;
  font-size: 32px;
}

.search-results p {
  font-family: 'Share Tech Mono', monospace;
}

.search-results {
  background-color: #c5c7d4;
  border: 1px solid var(--offBlack);
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 0 4px rgb(0, 0, 0);
}

.search-results:hover {
  box-shadow: 0 0 10px var(--offWhite);
  border: 1px solid var(--offWhite);
}

/*Search Results Anime*/

/* #anime-container {
    margin-top: 15vh;
} */

.search-results-anime h3 {
  font-family: 'VT323', monospace;
  font-weight: normal;
  color: var(--playstationBlue);
  text-decoration: underline;
  font-size: 32px;
  padding-bottom: 5px;
}

.search-results-anime p {
  font-family: 'Share Tech Mono', monospace;
}

.anime-rating-stream-div {
  padding-top: 5px;
}

.anime-stream {
  text-align: left;
}

/* 
#game-results, #anime-results{
    font-size: 3rem;
    font-family: 'Share Tech Mono', monospace;
    padding-bottom: 5px;
} */

.search-results-anime {
  background-color: #c5c7d4;
  border: 1px solid var(--offBlack);
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 0 4px rgb(0, 0, 0);
}

.search-results-anime:hover {
  box-shadow: 0 0 10px var(--offWhite);
  border: 1px solid var(--offWhite);
}

/*Footer*/

#footer {
  background-color: rgb(207, 199, 198);
}

#footer h4 {
  font-size: 2rem;
}

#githubIcon {
  color: black;
}

#githubIcon:hover {
  color: #0645ad;
}

/* Playstaion Theme */

.playstation-button {
  transition: all 0.2s ease-in-out;
}

.playstation-button:hover {
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px var(--playstationBlue);
}

