.title {
  font-family: 'VT323', monospace;
  color: #363636;
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 1.125;
  text-align: center;
  margin-bottom: 1rem;
}

.text {
  font-family: 'Share Tech Mono', monospace;
}

.details {
  background-color: #c5c7d4;
  border: 1px solid var(--offBlack);
  border-radius: 4px;
  box-shadow: 0 0 4px rgb(0, 0, 0);
}

.animeImage {
  text-align: center;
}
